@mobile: 320px;
@tablet: 768px;
@desktop: 1220px;

.hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  left: -1000px;
}

.main-catalog {
  max-width: 320px;
  margin: 0 auto;
}

.page-header__menu-main-sub-menu-elem-link--catalog:hover {
  color: #707070;
}

.page-header__menu-main-sub-menu-elem-link--catalog:active {
  color: #b7b7b7;
}

.main__main-text {
  font-size: 36px;
  line-height: 60px;
  color: #111111;
  font-family: "Oswald-Regular", "Arial", sans-serif;
}

.main-catalog__good {
  display: flex;
  border-left: 1px solid #ededed;
  border-right: 1px solid #ededed;
}

.main-catalog__good:nth-child(1n) {
  border-top: 1px solid #ededed;
}

.main-catalog__good-volume {
  display: flex;
  justify-content: space-between;
}

.main-catalog__good-header {
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  color: #111111;
  max-width: 74px;
  text-transform: uppercase;
  margin-bottom: 13px;
  margin-left: 1px;
}

.main-catalog__good-content {
  display: flex;
  flex-direction: column;
  max-width: 280px;
  margin: 0 auto;
}

.main-catalog__good-data {
  display: flex;
  flex-direction: column;
  margin-top: 22px;
}

.main-catalog__good-data-sub {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  line-height: 16px;
  font-family: "Arial", "Tahoma", sans-serif;
  margin-left: 1px;
  width: 100%;
}

.main-catalog__good-data-sub:last-child {
  margin-bottom: 0;
}

.main-catalog__good-order {
  background-color: #68b738;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  border: 0;
  color: #ffffff;
  padding: 10px 108px 10px 111px;
  margin-bottom: 24px;
  max-width: 282px;
}

.main-catalog__good-content-sub-text {
  display: flex;
}

.main-catalog__less-than-thousand {
  margin-right: 73px;
}

.main-catalog__more-than-thousand {
  margin-right: 67px;
}

@media (min-width: @mobile) {
  .main__catalog-mobile-image {
    display: block;
  }

  .main-catalog__plus-mobile {
    display: block;
  }

  .main-catalog__main-text {
    font-size: 36px;
    line-height: 60px;
    font-family: "Oswald-Regular", "Arial", sans-serif;
    padding-bottom: 2px;
    margin-top: 15px;
    margin-left: 22px;
    margin-bottom: 26px;
    display: flex;
  }

  .main__catalog--small-mobile-image {
    width: 64px;
    height: 78px;
  }

  .main__catalog--first-image {
    margin-top: 1px;
  }

  .main-catalog__good-order {
    margin-top: -4px;
    margin-left: 1px;
  }

  .main-catalog__good-order--third-image {
    margin-top: -5px;
  }

  .main-catalog__good-data-second {
    margin-top: 20px;
  }

  .main-catalog__good-order--last-child {
    margin-top: -3px;
  }
}

@media (min-width: @tablet) {
  .main-catalog {
    max-width: 761px;
  }

  .main-catalog__main-text {
    font-size: 60px;
    line-height: 60px;
    font-family: "Oswald-Regular", "Arial", sans-serif;
    padding-bottom: 2px;
    margin-top: 44px;
    margin-left: 28px;
    display: flex;
    margin-bottom: 165px;
    max-width: 431px;
  }

  .main__catalog-image {
    position: absolute;
    left: -37px;
    top: -49px;
  }

  .main-catalog__goods {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 auto;
    padding-left: 26px;
    padding-right: 15px;
    align-items: flex-start;
  }

  .main__catalog-mobile-image {
    display: none;
  }

  .main-catalog__good-content-sub-text {
    flex-direction: column;
    align-items: center;
