  }

  .main-catalog__good-price-sub {
    margin-left: 2px;
  }

  .main-catalog__good-order {
    background-color: #68b738;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 20px;
    font-family: "Oswald-Regular", "Arial", sans-serif;
    border: 0;
    color: #ffffff;
