@white: #fff;
@mobile: 320px;
@tablet: 768px;
@desktop: 1220px;
@retina-dpi: 144dpi;
@retina-dppx: 1.5dppx;

@font-face {
  font-family: "fontello";
  src: url("../font/fontello.eot?11710255");
  src: url("../font/fontello.eot?11710255#iefix") format("embedded-opentype"), url("../font/fontello.woff2?11710255") format("woff2"), url("../font/fontello.woff?11710255") format("woff"), url("../font/fontello.ttf?11710255") format("truetype"), url("../font/fontello.svg?11710255#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}


/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */


